<template>
  <div>
    <div class="warp">
      <div
        class="warp_center"
        v-for="(item, index) in this.$store.state.arr2"
        :key="index"
      >
        <div :class="item.str">
          <img
            :src="item.img"
            width="50px"
            height="50px"
            style="margin-top: 10px"
          />
        </div>
        <p>{{ item.text }}</p>
        <p>{{ item.text2 }}</p>
      </div>
    </div>
    <div class="box">
      <div class="box_top">
        <p>
          &emsp;超级企业信息&nbsp;
          <i class="el-icon-edit" style="color: #91a1a9"></i>
        </p>
        <p>
          当前超级企业为{{ this.$store.state.arr.status ? "启用" : "停用" }}状态
          <a class="a" @click="tan()"
            >{{ this.$store.state.arr.status ? "停用" : "启用" }}超级企业</a
          >&emsp;
        </p>
      </div>
      <div class="box_text">
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            ><span style="color: #f56c6c; margin-right: 2px">*</span
            >&nbsp;超级企业名称</span
          >&emsp;
          <span style="text-align: left; display: inline-block; width: 160px">{{
            this.$store.state.arr.ename
          }}</span>
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            >超级企业ID</span
          >&emsp;
          <span style="text-align: left; display: inline-block; width: 160px">{{
            this.$store.state.arr.id
          }}</span>
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            ><span style="color: #f56c6c; margin-right: 2px">*</span
            >&nbsp;企业域名</span
          >&emsp;<span
            style="text-align: left; display: inline-block; width: 160px"
            >{{ this.$store.state.arr.ipstr }}</span
          >
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            >最大坐席数</span
          >&emsp;
          <span style="text-align: left; display: inline-block; width: 160px">{{
            this.$store.state.arr.maxnum
          }}</span>
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            ><span style="color: #f56c6c; margin-right: 2px">*</span
            >&nbsp;超级管理员账号</span
          >&emsp;<span
            style="text-align: left; display: inline-block; width: 160px"
            >{{ this.$store.state.arr.adminname }}</span
          >
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            >最大登录并发总数</span
          >&emsp;
          <span
            style="
              text-align: left;
              display: inline-block;
              width: 160px;
              color: black;
            "
            >{{ this.$store.state.arr.maxconcurrency }}</span
          >
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            ><span style="color: #f56c6c; margin-right: 2px">*</span
            >&nbsp;邮箱</span
          >&emsp;<span
            style="
              text-align: left;
              display: inline-block;
              width: 160px;
              color: black;
            "
            >{{ this.$store.state.arr.adminemil }}</span
          >
        </p>
        <p>
          <span style="width: 160px; display: inline-block; text-align: right"
            >创建时间</span
          >&emsp;
          <span
            style="
              text-align: left;
              display: inline-block;
              width: 160px;
              color: black;
            "
            >{{ formattedCreateTime }}</span
          >
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { postDeactivate } from "../../../api/https";
export default {
  data() {
    return {
      arr: [],
      Status: "",
      StatuS: "",
      data: this.$store.state.arr,
    };
  },
  methods: {
    change() {
      this.data = this.$store.state.arr;
      console.log(this.data);
      // this.$set(this.arr,this.$store.state.arr)
    },
    bian() {
      this.Status = this.$store.state.arr.status ? "停用" : "启用";
      this.StatuS = this.$store.state.arr.status ? "启用" : "停用";
      console.log(
        this.Status,
        this.StatuS,
        this.$store.state.arr.status,
        this.data
      );
    },
    tan() {
      this.$confirm(
        `${
          this.$store.state.arr.status ? "停用" : "启用"
        }超级企业，其子企业也全部${
          this.$store.state.arr.status ? "停用" : "启用"
        }!`,
        "提示",
        {
          distinguishCancelAndClose: true,
          center: true,
        }
      )
        .then(() => {
          let a = !this.$store.state.arr.status;
          let obj = {
            id: this.$store.state.arr.id,
            status: a ? 1 : 0,
          };
          console.log(obj);
          postDeactivate(obj).then((res) => {
            console.log(res);
            this.bian();
            this.$store.dispatch("uparr", a ? 1 : 0);
            this.$emit("a")
          });
        })
        .catch((action) => {
          this.$message({
            type: "info",
            message:
              action === "cancel" ? "放弃保存并离开页面" : "停留在当前页面",
          });
        });
    },
    // changeColor(newColor) {
    //   this.$store.commit("setColor", newColor);
    // },
  },
  computed: {
    ...mapState({
      ArrData: (state) => state.arr,
    }),
    formattedCreateTime() {
      const times = this.$store.state.arr.create_time;
      const date = new Date(times * 1000);
      return `${date.getFullYear()}-${("0" + (date.getMonth() + 1)).slice(
        -2
      )}-${("0" + date.getDate()).slice(-2)} ${("0" + date.getHours()).slice(
        -2
      )}:${("0" + date.getMinutes()).slice(-2)}:${(
        "0" + date.getSeconds()
      ).slice(-2)}`;
    },
  },
  // created() {
  //   if (this.$store.state.arr.length > 0) {
  //     this.bian();
  //   } else {
  //     console.log("失败");
  //   }
  // },
};
</script>
