  width: 100%;
}

.box_top p:nth-child(1) {
  float: left;
}

.box_top p:nth-last-child(1) {
  float: right;
  color: #91a1be;
}

.box {
  width: 96%;
  border: 1px solid #edeef0;
  margin: auto;
  margin-top: 20px;
}

.el-main[data-v-63a42b6e] {
  line-height: 62px !important;
}

.warp_center {
  width: 16.6%;
}

.warp_center p {
  color: #91a1a9;
  font-size: 12px;
  height: 50px;
}

.warp_center p:nth-of-type(1) {
  font-size: 22px;
  color: black;
  height: 30px;
}

.warp {
  display: flex;
  justify-content: space-between;
  width: 96%;
  border: 1px solid #edeef0;
  margin: auto;
}

* {
  margin: 0;
  padding: 0;
}

.s_img {
  border-radius: 50%;
  margin: auto;
  width: 70px;
  height: 70px;
  margin-top: 20%;
}

.s_img1 {
  background: #5dacff;
}

.s_img2 {
  background: #ffab2d;
}

.s_img3 {
